body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, button, p, blockquote, th, td, form, fieldset, blockquote, iframe {
	margin: 0;
	padding: 0;
	-moz-text-size-adjust: none;
	-ms-text-size-adjust: none;
	-webkit-text-size-adjust: none;
	text-size-adjust: none; }

img {
	vertical-align: top;
	border: 0; }

fieldset {
	border: 0; }

ul, ol, li {
	list-style: none; }

hr {
	display: none; }

address {
	font-style: normal; }

iframe {
	border: 0; }

body {
	font-family: "애플 SD 산돌고딕 Neo", "Apple SD Gothic Neo", sans-serif;
	color: #222;
	line-height: 1.5;
	letter-spacing: -0.04em; }
