@import "./modules/reset";

body {
	padding: 10px; }
section {
	padding: 10px;
	border: 1px solid powderblue; }

ul {
	padding-left: 50px;
	&.list-no {
		li {
			list-style: none; } }
	li {
		list-style: initial; } }
h1 {
	font-size: 14px; }

.progess-wrap {
	padding-top: 10px; }
.progressbar-wrap {
	width: 150px;
	height: 15px;
	border-radius: 50px;
	border: 1px solid tomato;
	overflow: hidden;
	.bar {
		height: 15px;
		background: tomato; } }

.progress-done {
	margin-top: 20px; }

$ct-color: tomato;

.study-list {
	margin: 20px 0;
	h2 .desc {
		font-size: 12px;
		color: #555;
		&:before {
			content: '';
			display: inline-block;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			background: $ct-color;
			margin: 0 10px; } }
	article {
		margin: 10px 0;
		border: 1px solid orange; }
	.doing {
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: tomato;
			opacity: 0.4; } } }

.func {
	color: white;
	background: #999;
	padding: 0 10px;
	display: block;
	box-sizing: border-box; }

.done {
	ol {
		padding-left: 20px;
		li {
			list-style-type: decimal; } } }


#iframeWrap {
	margin-top: 30px;
	iframe {
		width: 45%;
		max-width: 400px;
		height: 300px;
		box-sizing: border-box;
		margin: 20px 0;
		float: left;
		border: 1px solid #000;
		&:nth-child(2n-1) {
			margin-right: 10%; } } }

.t-box {
	display: inline-block;
	color: white;
	background: #999;
	padding: 0 10px;
	box-sizing: border-box; }

// index.html
.see-all {
	.spec {
		border: 0 none;
		background: powderblue;
		h1 {
			font-size: 20px; }
		ul {
			padding-left: 0;
			li {
				list-style: none; } } }
	.progressive {
		display: block;
		line-height: 30px;
		margin-bottom: 25px;
		border: 1px solid black;
		font-size: 20px;
		text-align: center; }
	article {
		margin: 10px 0;
		padding: 0 5px;
		border: 1px solid orange;
		h2 {
			font-size: 20px;
			border-bottom: 1px solid orange; } } }

@import "./modules/func";
